import {preView} from '../../../commen/spot.js'
export default{
	name:'banner',
	props:{
		toDatas:Object,
		toCommentDetails:Array
	},
	data(){
		return{
			current:1,
			bannerImg:[]
		}
	},
	methods:{
		handleIndex(e){
			console.log(e)
			this.current = e.detail.current + 1
		},
		previewImg(index){
			preView(this.bannerImg,index)
			.then(res=>{
				console.log('预览成功')
			})
			.catch(err=>{
				console.log('预览失败')
			})
		}
	},
	watch:{
		toDatas(newDatas,oldDatas){
			this.bannerImg = newDatas.uploadImg.slice(0,3)
		}
	}
}
